/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear_derecha {
  display: flex;
  max-width: 90%;
  justify-content: flex-end;
}

.blog {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sp {
  color: #E08709;
}

.dark_mode {
  background-color: #1a1a1a;
}
.dark_mode p {
  color: white;
}
.dark_mode a, .dark_mode h1, .dark_mode h2, .dark_mode h3, .dark_mode h4 {
  color: #e1e1e1;
}
.dark_mode .anuncio,
.dark_mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark_mode .anuncio .precio,
.dark_mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark_mode .anuncio .icono,
.dark_mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark_mode .entrada_blog {
  background-color: #333333;
}
.dark_mode .formulario label, .dark_mode .formulario p, .dark_mode .formulario legend {
  color: #FFFFFF;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido_header {
    min-height: 70rem;
  }
}

.contenido_header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.titulo_principal {
  color: #FFFFFF;
  line-height: 1.5;
  display: none;
}
@media (min-width: 768px) {
  .titulo_principal {
    display: block;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark_mode_btn {
  width: 3rem;
  margin-bottom: 3rem;
  margin-top: 3rem;
  filter: invert(100%);
}
.derecha .dark_mode_btn:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark_mode_btn {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.3s linear;
  align-items: center;
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #71B100;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    opacity: 1;
    visibility: visible;
  }
}

@media (min-width: 768px) {
  .mobile_menu {
    display: none;
  }
}
.mobile_menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos_nosotros {
    display: grid;
    gap: 2;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono img {
  height: 10rem;
  text-align: center;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor_anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    width: 95%;
    margin: 0 auto;
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 4rem;
  max-width: 350px;
  margin: 10px auto;
}
.anuncio .contenido_anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos_caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
  border-left: 10px;
}
.iconos_caracteristicas li {
  flex: 1;
  display: flex;
  margin-left: 30px;
}
@media (min-width: 768px) {
  .iconos_caracteristicas li {
    margin-left: 10px;
  }
}
.iconos_caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos_caracteristicas li p {
  font-weight: 900;
}

.bnt_amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2px;
  display: block;
  border: none;
}
.bnt_amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.bnt_verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2px;
  display: inline-block;
  border: none;
  margin-top: 20px;
}
.bnt_verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.bnt_amarillo_block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2px;
  display: inline-block;
  border: none;
}
.bnt_amarillo_block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.no-webp .imagen_contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen_contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen_contacto {
  margin-top: 30px;
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 25px;
}
.imagen_contacto h2 {
  color: #FFFFFF;
}
.imagen_contacto p {
  color: #FFFFFF;
}

.seccion_blog {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  max-width: 90%;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .seccion_blog {
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    gap: 30px;
    max-width: 90%;
    margin: 0 auto;
  }
}
.item1 {
  border: 1px soli black;
}

.entrada_blog {
  margin-top: 20px;
  border: 1px solid rgba(102, 51, 153, 0.336);
  max-width: 400px;
  background-color: rgba(240, 248, 255, 0.514);
  border-radius: 5px;
}
.entrada_blog picture {
  height: 190px;
}
.entrada_blog img {
  height: 188px;
}
.entrada_blog h4 {
  font-size: 16px;
  padding-left: 10px;
}
.entrada_blog p {
  font-size: 15px;
  padding-left: 10px;
  line-height: 1.3;
}
.entrada_blog span {
  color: #E08709;
}

@media (min-width: 768px) {
  .entrada_blog {
    display: flex;
    height: 190px;
    max-width: 600px;
    margin-top: 20px;
  }
}
.card_terstimonio {
  max-width: 300px;
  height: 390px;
  background-color: #71B100;
  border-radius: 10px;
}
.card_terstimonio p {
  padding: 20px 30px;
  padding-left: 80px;
}

.footer {
  background-color: #333333;
}
.footer .contenedor_footer {
  padding-top: 20px;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.contenedor_nosotros {
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .itemnonosotros {
    width: 50%;
  }
  .itemnonosotros p {
    padding: 0px 30px;
  }
  .contenedor_nosotros {
    display: flex;
    flex-direction: row;
  }
  .contenedor_nosotros img {
    width: 100%;
  }
}
.itemnonosotros {
  width: 100%;
}

.formulario p {
  font-size: 1rem;
  margin: 1rem 0 0 0;
  color: #333333;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  width: 100%;
  display: block;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma_contacto {
  width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}