
.seccion_blog{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    // border: 1px solid red;
    gap:30px;
    max-width: 90%;
    margin: 0 auto;

}

@include tablet{
    .seccion_blog{
        display: flex;
        justify-content: center;
        flex-direction: row;
        align-items: center;
        // border: 1px solid red;
        gap:30px;
        max-width: 90%;
        margin: 0 auto;
    }
}
.item1{
    border: 1px soli black;
    
}
.entrada_blog{
    margin-top: 20px;
    border: 1px solid rgba(102, 51, 153, 0.336);
    max-width: 400px;
    background-color: rgba(240, 248, 255, 0.514);
    border-radius: 5px;
    picture{
        height: 190px;
    }
    img{
       
        height: 188px;
       
        }
        h4{
            font-size: 16px;
            padding-left: 10px;
        }

        p{
            font-size: 15px;
            padding-left: 10px;
            line-height: 1.3;
        }
        span{
            color:$amarillo;
        }
}
@include tablet{
    .entrada_blog {
     
        display: flex;
        height: 190px;
        max-width: 600px;
        margin-top: 20px;
       
    }
   
   
}

.card_terstimonio{
    // border: 1px solid greenyellow;
    max-width: 300px;
    height: 390px;
    background-color: $verde;
    border-radius: 10px;
    p{
        padding: 20px 30px;
        padding-left: 80px;
    }
}