@include tablet {
    .contenedor_anuncios {
        @include grid(3, 2rem);
        width: 95%;
        margin: 0 auto;
    }
}

.anuncio {
    background-color: lighten($gris, 5);
    border: 1px solid darken($gris, 10);
    margin-bottom: 4rem;
    max-width: 350px;
    margin: 10px auto;

    .contenido_anuncio {
        padding: 2rem;
    }

    h3, p {
        margin: 0;
    }
 
}
.precio {
    color: $verde;
    font-weight: $bold;
    font-size: 3rem;
}

.iconos_caracteristicas {
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;
    border-left: 10px;

    li {
        flex: 1;
        display: flex;
        margin-left: 30px;
        @include tablet{
            margin-left: 10px;
        }

        img {
            flex:  0 0 3rem;
            margin-right: 1rem;
        }
        p {
            font-weight: $black;
        }
    }
}