
.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet{
        align-items: flex-end;
    }

    .dark_mode_btn{
        width: 3rem;
        margin-bottom: 3rem;
        margin-top: 3rem;
        filter: invert(100%);
        &:hover{
            cursor: pointer;
        }
        
        @include tablet{
            margin-top: 0;
        }
    }
}

.barra{
    display: flex;
    flex-direction: column;
    
    padding-top: 3rem;
    @include tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}
.navegacion{

    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity 0.5s linear,height .3s linear;
    // display: none;
   align-items: center;
    a{
        color:$blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        &:hover{
           color: $verde;
        }
        @include tablet{
            margin-right: 2rem;
            font-size: 1.8rem;
            &:last-of-type{
                margin-right: 0;
            }
        }
    }
    @include tablet{
        display: flex;
        opacity: 1;
        visibility: visible;
    }
}

.mobile_menu{
@include tablet{
   display: none;
    }

    img{
        width: 5rem;
    };
}

.mostrar{
    // display: flex;
    // flex-direction: column;

    // @include tablet{
    //     flex-direction: row;
    // }

    visibility: visible;
    opacity: 1;
    height: auto;
}