
.dark_mode{
    background-color: darken($grisOscuro,10);
    p{
        color: white;
    }
    a,h1,h2,h3,h4{
        color: $gris;
    }
    .anuncio,
     .resumen-propiedad{
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;
        .precio{
            color: $verde;
        }
        .icono{
            filter: invert(100%);
        }
    }

    .entrada_blog{
        background-color: $grisOscuro;
    }

    .formulario{

        label, p, legend{
   color: $blanco;
        }

        
    }

        
    }

    